@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Roboto.woff2") format("woff2"), url("../fonts/Roboto.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Robotomedium.woff2") format("woff2"), url("../fonts/Robotomedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Robotobold.woff2") format("woff2"), url("../fonts/Robotobold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.title {
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  margin: 0;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.button {
  margin: 0;
  padding: 8px 30px;
  background-color: #ffffff;
  border: 1px solid #cfcfcf;
  border-radius: 3px;
  min-height: 40px;
  cursor: pointer;
}
.button--active {
  background-color: #3366ff;
  color: #ffffff;
  width: 125px;
  margin-left: auto;
}

.input {
  border: none;
  margin: 0;
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: "Roboto", "Arial", "Helvetica", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  background-color: #ffffff;
}

.page__body {
  margin: 0;
}

.body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  color: #000000;
  background-color: #ffffff;
}

.body__page-header {
  padding: 26px 10px 22px;
}
@media (min-width: 768px) {
  .body__page-header {
    padding: 26px 30px 23px;
  }
}

.body__page-main {
  padding: 0 10px;
}
@media (min-width: 768px) {
  .body__page-main {
    padding: 0 30px;
  }
}
@media (min-width: 1200px) {
  .body__page-main {
    padding: 0;
  }
}

.page-header {
  display: flex;
  gap: 25px;
  align-items: center;
  border-bottom: 1px solid #cfcfcf;
}

.page-header__logo {
  margin-right: auto;
}

.page-header__logo-image {
  display: block;
}

.page-header__basket {
  position: relative;
}

.page-header__icon {
  display: block;
}

.page-header__basket-sup-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: -6px;
  right: 0;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #3366ff;
}

.page-header__basket-sup-icon-quantity {
  font-size: 10px;
  line-height: 12px;
  color: #ffffff;
}

.nav {
  position: relative;
}
@media (min-width: 1200px) {
  .nav {
    display: none;
  }
}

.nav__toggle {
  position: relative;
  width: 29px;
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
}
.nav__toggle::before {
  content: "";
  position: absolute;
  top: -13px;
  left: 0;
  width: 29px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 10px 0 0 #000000, 0 20px 0 0 #000000;
}

.page-main {
  flex-grow: 1;
}
@media (min-width: 1200px) {
  .page-main {
    display: grid;
    grid-template-columns: 1fr 410px;
  }
}

.page-main__left {
  padding-top: 60px;
  padding-bottom: 280px;
  position: relative;
}
@media (min-width: 768px) {
  .page-main__left {
    padding-bottom: 190px;
  }
}
@media (min-width: 1200px) {
  .page-main__left::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 1px;
    min-height: 100%;
    background-color: #cfcfcf;
  }
}

.page-main__legend {
  margin-bottom: 60px;
}
@media (min-width: 768px) {
  .page-main__legend {
    margin: 0 auto 117px;
  }
}
@media (min-width: 1200px) {
  .page-main__legend {
    width: 661px;
    margin: 0 auto 60px;
  }
}

@media (min-width: 1200px) {
  .page-main__billing-info {
    margin: 0 50px;
  }
}
@media (min-width: 1640px) {
  .page-main__billing-info {
    margin: 0 auto;
  }
}

.legend {
  width: 300px;
}
@media (min-width: 768px) {
  .legend {
    width: 639px;
  }
}
@media (min-width: 1200px) {
  .legend {
    width: 661px;
  }
}
@media (min-width: 1640px) {
  .legend {
    width: 1101px;
  }
}

.legend__map-list {
  position: relative;
  display: flex;
  margin: 0 auto 9px;
}
.legend__map-list::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 33px;
  width: 147px;
  height: 2px;
  background-color: #3366ff;
}
.legend__map-list::before {
  content: "";
  position: absolute;
  top: 8px;
  right: 31px;
  width: 71px;
  height: 2px;
  background-color: #d5d5d5;
}
@media (min-width: 768px) {
  .legend__map-list {
    justify-content: space-between;
    margin: 0 auto;
  }
  .legend__map-list::after {
    content: "";
    left: 34px;
    width: 367px;
  }
  .legend__map-list::before {
    content: "";
    right: 31px;
    width: 191px;
  }
}
@media (min-width: 1200px) {
  .legend__map-list::after {
    content: "";
    left: 33px;
    width: 375px;
  }
  .legend__map-list::before {
    content: "";
    width: 206px;
  }
}
@media (min-width: 1640px) {
  .legend__map-list::after {
    content: "";
    width: 683px;
  }
  .legend__map-list::before {
    content: "";
    width: 338px;
  }
}

.legend__item:nth-child(1) {
  padding-right: 26px;
}
@media (min-width: 768px) {
  .legend__item:nth-child(1) {
    padding-right: 125px;
  }
}
@media (min-width: 1200px) {
  .legend__item:nth-child(1) {
    padding-right: 118px;
  }
}
@media (min-width: 1640px) {
  .legend__item:nth-child(1) {
    padding-right: 154px;
  }
}

.legend__item:nth-child(2) {
  padding-right: 38px;
}
@media (min-width: 768px) {
  .legend__item:nth-child(2) {
    padding-right: 158px;
  }
}
@media (min-width: 1200px) {
  .legend__item:nth-child(2) {
    padding-right: 142px;
  }
}
@media (min-width: 1640px) {
  .legend__item:nth-child(2) {
    padding-right: 151px;
  }
}

.legend__item:nth-child(3) {
  padding-right: 48px;
}
@media (min-width: 768px) {
  .legend__item:nth-child(3) {
    padding-right: 166px;
  }
}

.legend__item {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  row-gap: 11px;
  align-items: center;
  width: min-content;
}

.legend__box,
.legend__number,
.legend__title {
  margin: 0;
}

.legend__box {
  width: 16px;
  height: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: #3366ff;
}
.legend__box--before-the-last {
  background-color: transparent;
  border: 1px solid #3366ff;
}
.legend__box--last {
  background-color: transparent;
  border: 1px solid #d5d5d5;
}

.legend__number {
  color: #ffffff;
  font-weight: 500;
  font-size: 10px;
}
.legend__number--before-the-last {
  color: #000000;
}
.legend__number--last {
  color: #d5d5d5;
}

@media (min-width: 1640px) {
  .billing-info {
    width: 660px;
  }
}

.billing-info__title,
.billing-info__types-of-payments {
  margin-bottom: 17px;
}

@media (min-width: 768px) {
  .billing-info__types-of-payments {
    margin: 0 50px 40px;
  }
}
@media (min-width: 1200px) {
  .billing-info__types-of-payments {
    margin: 0 30px 40px;
  }
}
@media (min-width: 1640px) {
  .billing-info__types-of-payments {
    margin: 0 15px 40px;
  }
}

@media (min-width: 768px) {
  .billing-info__title {
    display: none;
  }
}
@media (min-width: 1200px) {
  .billing-info__title {
    display: block;
    margin: 0 30px 37px;
  }
}
@media (min-width: 1640px) {
  .billing-info__title {
    display: block;
    margin: 0 15px 37px;
  }
}

.billing-info__card-details {
  margin-bottom: 21px;
}

.billing-info__special-proposal {
  margin-bottom: 45px;
}
@media (min-width: 768px) {
  .billing-info__special-proposal {
    margin: 0 50px 40px;
  }
}
@media (min-width: 1200px) {
  .billing-info__special-proposal {
    margin: 0 30px 39px;
  }
}
@media (min-width: 1640px) {
  .billing-info__special-proposal {
    margin: 0 15px 39px;
  }
}

.billing-info__input {
  padding: 0;
  background-color: #ededf1;
  border-radius: 3px;
}

.billing-info__control-mark {
  position: relative;
  display: block;
  width: 16px;
  height: 16px;
  background: #ffffff;
  border: 1px solid #cfcfcf;
  border-radius: 50%;
}
.billing-info__control-mark::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  width: 8px;
  height: 8px;
  background: #3366ff;
  border-radius: 50%;
}

@media (min-width: 768px) {
  .billing-info__billing-info-buttons {
    margin: 0 50px;
  }
}
@media (min-width: 1200px) {
  .billing-info__billing-info-buttons {
    margin: 0 30px;
  }
}
@media (min-width: 1640px) {
  .billing-info__billing-info-buttons {
    margin: 0 15px;
  }
}

.billing-info__input[type=radio]:checked + .billing-info__control-mark::before {
  display: block;
}

.types-of-payments {
  position: relative;
  display: flex;
  align-items: center;
  gap: 21px;
}
@media (min-width: 768px) {
  .types-of-payments {
    justify-content: start;
    column-gap: 82px;
  }
}

.types-of-payments__icon {
  width: 94px;
  height: 23px;
  background: url("../img/icon-pay-pal.svg") no-repeat 7px 0;
}

@media (min-width: 768px) {
  .card-details {
    width: 550px;
    margin: 0 20px 40px;
    padding: 0 30px 45px;
    border: 1px solid #cfcfcf;
    border-radius: 3px;
    box-shadow: 0 0 30px #cfcfcf;
    box-sizing: border-box;
  }
}
@media (min-width: 1200px) {
  .card-details {
    margin-left: 0;
  }
}
@media (min-width: 1640px) {
  .card-details {
    margin-left: -15px;
  }
}

.card-details__container {
  display: flex;
  flex-direction: column;
  padding-bottom: 15px;
}
.card-details__container--code {
  width: 23.2%;
}
@media (min-width: 768px) {
  .card-details__container {
    padding-bottom: 0;
  }
  .card-details__container--code {
    width: 20.2%;
  }
}

.card-details__input {
  width: 100%;
  min-height: 40px;
  padding: 10px;
  background: #ededf1;
  border-radius: 3px;
}

.card-details__label {
  position: relative;
  padding: 5px 0 0;
  font-weight: 500;
  font-size: 12px;
  line-height: 24px;
  color: #555770;
}
@media (min-width: 768px) {
  .card-details__label {
    padding-top: 20px;
    padding-bottom: 0;
  }
}

.card-details__cvv-icon {
  position: absolute;
  top: 10px;
  width: 13px;
  height: 13px;
  margin-left: 7px;
  background: #dbdbdb;
  border-radius: 50%;
}
.card-details__cvv-icon::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 6px;
  height: 10px;
  background: url("../img/question-mark.svg") no-repeat;
}
@media (min-width: 768px) {
  .card-details__cvv-icon {
    top: 26px;
  }
}

.wrapper {
  display: flex;
  gap: 5px;
}
.wrapper--types-of-payments, .wrapper--special-proposal {
  align-items: center;
  gap: 7px;
}
@media (min-width: 768px) {
  .wrapper {
    gap: 30px;
  }
  .wrapper--types-of-payments, .wrapper--special-proposal {
    gap: 9px;
  }
}
@media (min-width: 1200px) {
  .wrapper--form {
    gap: 10px;
  }
  .wrapper--calculation {
    gap: none;
    justify-content: space-between;
  }
}

.special-proposal {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.special-proposal__credit-card-info {
  color: #8c8c8c;
}

.special-proposal__control-input {
  align-self: flex-start;
}

.special-proposal__control-label {
  max-width: 238px;
}
@media (min-width: 768px) {
  .special-proposal__control-label {
    max-width: max-content;
  }
}

.special-proposal__control-mark {
  position: relative;
  display: block;
  width: 16px;
  height: 16px;
  background: #ffffff;
  border: 1px solid #cfcfcf;
}
.special-proposal__control-mark::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  width: 8px;
  height: 8px;
  background: #3366ff;
}

.special-proposal__input[type=checkbox]:checked + .special-proposal__control-mark::before {
  display: block;
}

.billing-info-buttons {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.product {
  display: none;
}
@media (min-width: 1200px) {
  .product {
    display: block;
  }
}

@media (min-width: 1200px) {
  .product__promo-code {
    padding-bottom: 40px;
  }
}

@media (min-width: 1200px) {
  .product__my-basket,
.product__promo-code,
.product__calculation {
    padding: 40px;
    border-bottom: 1px solid #cfcfcf;
  }
}

@media (min-width: 1200px) {
  .product__calculation {
    padding: 40px;
    border-bottom: none;
  }
}

.my-basket__title {
  margin-bottom: 30px;
}

.my-basket__link {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
  color: #3366ff;
  font-size: 18px;
  text-decoration: none;
}

.my-basket__icon {
  fill: #3366ff;
}

.product-list__product-card {
  display: grid;
  grid-template-columns: 30.2% 26% 1fr 1fr;
  grid-template-rows: auto 1fr;
  gap: 12px;
  align-items: start;
  margin-bottom: 39px;
}

.product-list__product-card:last-child {
  margin-bottom: 0;
}

.product-list__link {
  grid-row: 1/3;
}

.product-list__image {
  display: block;
}

.product-list__title {
  grid-column: 2/4;
  min-height: 32px;
  margin: 0;
}

.product-list__quantity {
  grid-column: 2/3;
  display: flex;
}

.product-list__price {
  grid-column: 3/4;
  margin: 0;
  width: min-content;
  padding: 7px 0 0 13px;
}
@media (min-width: 1640px) {
  .product-list__price {
    width: max-content;
  }
}

.product-list__button {
  grid-column: 4/-1;
  grid-row: 1/2;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
  position: relative;
  width: 18px;
  height: 18px;
  margin-left: auto;
  cursor: pointer;
}
.product-list__button::after, .product-list__button::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  width: 18px;
  height: 1px;
  background-color: #cfcfcf;
}
.product-list__button::before {
  content: "";
  transform: translate(-50%, -50%) rotate(-45deg);
}

.product-list__range-button {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: transparent;
  border: 1px solid #000000;
  position: relative;
  cursor: pointer;
}
.product-list__range-button::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 10px;
  height: 1px;
  background-color: #000000;
}
.product-list__range-button--positive::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1px;
  height: 10px;
  background-color: #000000;
}

.product-list__range-input {
  width: 100%;
  text-align: center;
  background-color: transparent;
  border: none;
  outline: none;
  padding: 0 6px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
}

.promo-code__title {
  margin-bottom: 29px;
}

.promo-code__input {
  width: 100%;
  min-height: 40px;
  padding: 10px;
  background: #ededf1;
  border-radius: 3px;
}

.calculation-list {
  width: 314px;
}

.calculation-list__title {
  line-height: 34px;
}
.calculation-list__title--total {
  font-size: 18px;
  font-weight: 700;
}

.calculation-list__price {
  line-height: 34px;
  margin: 0;
}
.calculation-list__price--total {
  font-size: 18px;
  font-weight: 700;
}